import React from 'react';
import {Col, Container, Row} from "react-bootstrap";
import "./about.css"
import SectionTitle from "../gui/SectionTitle";

const Education = React.forwardRef((props, ref) => {

    return (
        <Container className="w-100 h-100 p-5" ref={ref}>
            <Row>
                <Col>
                    <SectionTitle title="Education" textColor="text-dark"/>
                </Col>
            </Row>
            <Row>
                <Col>
                    <h4 className="text-dark">University of Saint Louis, Tuguegarao, Cagayan Valley</h4>
                    <h5 className="text-dark">Bachelor of Science in Computer Science</h5>
                    <h6 className="text-dark">GRADUATED IN APRIL 2017</h6>
                </Col>
            </Row>
        </Container>
    );
});
export default Education;