import React from 'react';
import {Col, Container, Row} from "react-bootstrap";
import SectionTitle from "../gui/SectionTitle";
import "./workexp.css"

const WorkExperience = React.forwardRef((props, ref) => {

    return (
        <Container id="experience-container" className="h-100 w-100 p-5" ref={ref}>
            <Row>
                <Col className="p-5">
                    <SectionTitle title="Work Experience" textColor="text-white"/>
                </Col>
            </Row>
            <Row className="m-auto">
                <Col className="col-4">
                </Col>
                <Col className="text-right col-8">
                    <h4>Jr. Software Developer / Philippine General Hospital</h4>
                    <h6>MARCH 2019 – MARCH 2020</h6>
                    <ul className="text-right p-2">
                        <li>Develop a price portal management for medical drug items</li>
                        <li>Maintained and fixed unwanted bugs</li>
                    </ul>
                </Col>
            </Row>
            <Row>
                <Col className="text-left col-8">
                    <h4>Jr. Software Developer / Codegum Inc. </h4>
                    <h6>OCTOBER 2017 – NOVEMBER 2018</h6>
                    <ul className="text-left p-2">
                        <li>Authenticated user AWS credentials to make request from Amazon S3</li>
                        <li>Added real time notification feature using Firebase Cloud Messaging</li>
                        <li>Implemented Amazon S3 for the project to save and retrieve data from cloud</li>
                        <li>Integrated Facebook login function using Facebook API for android. User can login via
                            Facebook account
                        </li>
                        <li>Integrated Google Sign-In function using Google API for android. User can login via Google
                            account
                        </li>
                        <li>Designed mobile-friendly UI/UX with android XML. App responsiveness increased</li>
                        <li>Developed search and pagination of employee back-end module using Hibernate, SQL and Spring
                            Boot
                        </li>
                        <li>Developed back-end module for creating, updating and displaying information of an employee
                            records
                        </li>

                    </ul>
                </Col>
                <Col className="text-left col-4">
                </Col>
            </Row>
        </Container>
    );
});
export default WorkExperience;